import {SpinnerService} from './../../../../../../../../../../core/services/comon-services/spinner/spinner.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {BookInReadListDetail} from 'src/app/core/models/customer/book/response/BookInReadListDetail';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {BookService} from 'src/app/core/services/customer/book/book.service';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {CommentProgressResponse} from 'src/app/core/models/customer/book/request/CommentProgressResponse';

@Component({
	selector: 'app-customer-book-read-list-detail',
	templateUrl: './customer-book-read-list-detail.component.html',
	styleUrls: ['./customer-book-read-list-detail.component.less'],
	providers: [DestroyService]
})
export class CustomerBookReadListDetailComponent implements OnInit {
	isLoading = true; //skelton
	isNotHaveData: boolean = false;
	readListId: number = 1;
	languageCode = 'vn';
	remaningDays = '0';
	totalDays = '0';
	readDays = 0;
	readProgressIdEdit = 0;
	commentProgressIdEdit = 0;
	bookInReadListDetail: BookInReadListDetail = {} as BookInReadListDetail;
	commentprogresses: Array<CommentProgressResponse> = [];
	readPageCountPercent = 0;
	isAddReadProgressModalVisible = false;
	isEditReadProgressModalVisible = false;
	isAddCommentProgressModalVisible = false;
	isEditCommentProgressModalVisible = false;
	customerAddReadProgressModalForm!: FormGroup;
	customerEditReadProgressModalForm!: FormGroup;
	customerAddCommentProgressModalForm!: FormGroup;
	customerEditCommentProgressModalForm!: FormGroup;

	constructor(
		private route: ActivatedRoute,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private notificationService: NotificationService,
		private bookService: BookService,
		private formBuilder: FormBuilder,
		private spinner: SpinnerService
	) {}

	ngOnInit(): void {
		this.isLoading = true;
		if (this.route.snapshot.paramMap.get('readListId')) {
			this.readListId = +this.route.snapshot.paramMap.get('readListId');
			this.getBookInReadListDetail(this.readListId);
			this.getCommentProgresses(this.readListId);
		}
	}
	getBookInReadListDetail(readListId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.bookService
			.getBookInReadListDetail(readListId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.bookInReadListDetail = result.resultObj;
								this.caculateRemainingDays(result.resultObj.endDate);
								this.caculateTotalDays(result.resultObj.startDate, result.resultObj.endDate);
								this.readPageCountPercent = Math.floor(
									(result.resultObj.readPageCount / result.resultObj.pageCount) * 100
								);
								this.readDays = result.resultObj.readProgresses.length;
							} else {
								this.isNotHaveData = true;
							}
						} else {
							this.isNotHaveData = true;
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.isLoading = false;
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					}
				},
				(error) => {
					this.isLoading = false;
					this.isNotHaveData = true;
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	caculateRemainingDays(endDate: Date) {
		let count = Math.floor((new Date(endDate).getTime() - new Date(Date.now()).getTime()) / (1000 * 60 * 60 * 24));
		if (count > 0) {
			this.remaningDays = count.toString() + ' ngày';
		} else {
			this.remaningDays = 'Đã Hết Thời Gian';
		}
	}
	caculateTotalDays(startDate: Date, endDate: Date) {
		let count = Math.floor((new Date(endDate).getTime() - new Date(startDate).getTime()) / (1000 * 60 * 60 * 24));
		if (count > 0) {
			this.totalDays = count.toString();
		} else {
			this.totalDays = '0';
		}
	}
	initAddReadProgressForm() {
		try {
			this.customerAddReadProgressModalForm = this.formBuilder.group({
				readPageToday: [0, [Validators.required, Validators.minLength(1)]],
				readPageComment: ['', [Validators.required, Validators.minLength(1)]]
			});
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	showAddReadProgressModal() {
		this.isAddReadProgressModalVisible = !this.isAddReadProgressModalVisible;
		this.initAddReadProgressForm();
	}
	handleAddReadProgressModalCancel() {
		this.isAddReadProgressModalVisible = false;
	}
	handleAddReadProgressModalOk() {
		this.handleAddReadProgressModalCancel();
		const val = this.customerAddReadProgressModalForm.value;
		console.log(val.readPageToday);
		if (val.readPageToday && val.readPageToday > 0) {
			if (this.readListId) {
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
				this.bookService
					.postReadProgress({readListId: this.readListId, readPageCount: val.readPageToday})
					.subscribe(
						(result) => {
							if (result) {
								//console.log(result);
								if (result.isSuccessed) {
									if (result.resultObj) {
										this.getBookInReadListDetail(this.readListId);
										if (result.message) {
											this.notificationService.showSuccessNotification(result.message);
										}
									}
								} else {
									if (result.message) {
										this.notificationService.showErrorNotification(result.message);
									}
								}
							}
							this.handleAddReadProgressModalCancel();
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
						},
						(error) => {
							this.appErrorHandler.handleErrorWithNoti(error);
						}
					);
			}
		} else {
		}
	}
	initEditReadProgressForm() {
		try {
			this.customerEditReadProgressModalForm = this.formBuilder.group({
				readPageEdit: [0, [Validators.required, Validators.minLength(1)]]
			});
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	showEditReadProgressModal(readProgressId: number) {
		this.readProgressIdEdit = readProgressId;
		this.initEditReadProgressForm();
		this.isEditReadProgressModalVisible = !this.isEditReadProgressModalVisible;
	}
	handleEditReadProgressModalCancel() {
		this.isEditReadProgressModalVisible = false;
	}
	handleEditReadProgressModalOk() {
		this.handleEditReadProgressModalCancel();
		const val = this.customerEditReadProgressModalForm.value;
		console.log(this.readProgressIdEdit);
		console.log(val.readPageEdit);
		if (val.readPageEdit > 0) {
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			this.bookService
				.putReadProgress({readProgressId: this.readProgressIdEdit, readPageCount: val.readPageEdit})
				.subscribe(
					(result) => {
						if (result) {
							//console.log(result);
							if (result.isSuccessed) {
								if (result.resultObj) {
									this.getBookInReadListDetail(this.readListId);
									if (result.message) {
										this.notificationService.showSuccessNotification(result.message);
									}
								}
							} else {
								if (result.message) {
									this.notificationService.showErrorNotification(result.message);
								}
							}
						}
						this.handleEditReadProgressModalCancel();
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		}
	}
	initAddCommentProgressForm() {
		try {
			this.customerAddCommentProgressModalForm = this.formBuilder.group({
				quote: ['', [Validators.required, Validators.minLength(1)]],
				pageNumber: [0, [Validators.required, Validators.minLength(1)]],
				comment: ['', [Validators.required, Validators.minLength(1)]]
			});
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	showAddCommentProgressModal() {
		this.isAddCommentProgressModalVisible = !this.isAddCommentProgressModalVisible;
		this.initAddCommentProgressForm();
	}
	handleAddCommentProgressModalCancel() {
		this.isAddCommentProgressModalVisible = false;
	}
	handleAddCommentProgressModalOk() {
		this.handleAddCommentProgressModalCancel();
		const val = this.customerAddCommentProgressModalForm.value;
		if (this.readListId) {
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			this.bookService
				.postCommentProgress({
					readListId: this.readListId,
					quote: val.quote,
					pageNumber: val.pageNumber,
					comment: val.comment
				})
				.subscribe(
					(result) => {
						if (result) {
							console.log(result);
							if (result.isSuccessed) {
								this.getCommentProgresses(this.readListId);
								if (result.message) {
									this.notificationService.showSuccessNotification(result.message);
								}
							} else {
								if (result.message) {
									this.notificationService.showErrorNotification(result.message);
								}
							}
						}
						this.handleAddReadProgressModalCancel();
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
					}
				);
		}
	}
	getCommentProgresses(readListId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.bookService
			.getCommentProgresses(readListId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.commentprogresses = result.resultObj;
							} else {
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.isLoading = false;
						this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					}
				},
				(error) => {
					this.isLoading = false;

					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}

	initEditCommentProgressForm(commentProgressId: number) {
		try {
			this.customerEditCommentProgressModalForm = this.formBuilder.group({
				quote: ['', [Validators.required, Validators.minLength(1)]],
				pageNumber: [0, [Validators.required, Validators.minLength(1)]],
				comment: ['', [Validators.required, Validators.minLength(1)]]
			});
			this.initEditCommentProgressFromData(commentProgressId);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	showEditCommentProgressModal(commentProgressIdEdit: number) {
		this.commentProgressIdEdit = commentProgressIdEdit;
		this.isEditCommentProgressModalVisible = !this.isEditCommentProgressModalVisible;
		this.initEditCommentProgressForm(commentProgressIdEdit);
	}
	handleEditCommentProgressModalCancel() {
		this.isEditCommentProgressModalVisible = false;
	}
	handleEditCommentProgressModalOk() {
		this.handleEditCommentProgressModalCancel();
		const val = this.customerEditCommentProgressModalForm.value;

		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		this.bookService
			.putCommentProgress({
				readListId: this.readListId,
				commentProgressId: this.commentProgressIdEdit,
				quote: val.quote,
				pageNumber: val.pageNumber,
				comment: val.comment
			})
			.subscribe(
				(result) => {
					if (result) {
						//console.log(result);
						if (result.isSuccessed) {
							this.getCommentProgresses(this.readListId);
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
					}
					this.handleAddReadProgressModalCancel();
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	initEditCommentProgressFromData(commentProgressId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		let commentProgress = this.commentprogresses.find((c) => c.commentProgressId == commentProgressId);
		setTimeout(() => {
			this.customerEditCommentProgressModalForm.setValue({
				quote: commentProgress?.quote != null ? commentProgress?.quote : '',
				pageNumber: commentProgress?.pageNumber != null ? commentProgress?.pageNumber : 0,
				comment: commentProgress?.comment != null ? commentProgress?.comment : ''
			});
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
		}, 5000);
	}
	changeIsShare(commentProgressId: number) {
		this.bookService.postIsShareCommentProgress(commentProgressId).subscribe(
			(result) => {
				if (result) {
					//console.log(result);
					if (result.isSuccessed) {
						this.getCommentProgresses(this.readListId);
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
						}
					}
				}
				this.handleAddReadProgressModalCancel();
				this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
}
