import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {Component, OnInit} from '@angular/core';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {BookService} from 'src/app/core/services/customer/book/book.service';
import {environment} from 'src/environments/environment';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {BookBoughtResponse} from 'src/app/core/models/customer/book/response/BookBoughtResponse';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
@Component({
	selector: 'app-customer-reading-progress',
	templateUrl: './customer-reading-progress.component.html',
	styleUrls: ['./customer-reading-progress.component.less'],
	providers: [DestroyService]
})
export class CustomerReadingProgressComponent implements OnInit {
	isLoading = true; //skelton
	isNotHaveData: boolean = false;
	host: string = environment.apiUrl;
	pageResult: PagedResult<BookBoughtResponse>;
	constructor(
		private bookService: BookService,
		// private route: ActivatedRoute,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private destroy$: DestroyService // private authService: AuthService, // private suggestService: SuggestService, // private appErrorHandler: AppErrorHandler, // private ratingService: RatingService
	) {
		this.pageResult = {} as PagedResult<BookBoughtResponse>;
	}
	ngOnInit(): void {
		this.getBookBought(1);
	}
	getBookBought(pageIndex: number) {
		this.bookService
			.getBookBought({pageIndex: pageIndex, pageSize: 10})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.pageResult = result.resultObj;
							} else {
								this.isNotHaveData = true;
							}
						} else {
							this.isNotHaveData = true;
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
						this.isLoading = false;
					}
				},
				(error) => {
					this.isLoading = false;
					this.isNotHaveData = true;
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
}
