import {HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {CustomerUrlBase} from './../customer-url-base';
@Injectable({
	providedIn: 'root'
})
export class CustomerUrlBook extends CustomerUrlBase {
	getBookBought(request: PagingRequestBase): string {
		let params = new HttpParams()
			.set('pageindex', request.pageIndex == undefined ? '1' : request.pageIndex.toString())
			.set('pagesize', request.pageSize == undefined ? '10' : request.pageSize.toString());
		return this.customerUrl + '/book/bought?' + params;
	}
	getBookInReadList(request: PagingRequestBase): string {
		let params = new HttpParams()
			.set('pageindex', request.pageIndex == undefined ? '1' : request.pageIndex.toString())
			.set('pagesize', request.pageSize == undefined ? '10' : request.pageSize.toString());
		return this.customerUrl + '/book/read-list?' + params;
	}
	getBookInReadListDetail(readListId: number): string {
		return this.customerUrl + '/book/read-list/' + readListId;
	}
	postBookToReadList(): string {
		return this.customerUrl + '/book/read-list';
	}
	postReadProgress(): string {
		return this.customerUrl + '/book/read-progress';
	}
	putReadProgress(): string {
		return this.customerUrl + '/book/read-progress';
	}
	deleteBookFromReadList(productId: Number) {
		return this.customerUrl + '/book/read-list/' + productId;
	}
	//Comment progress
	postCommentProgress(): string {
		return this.customerUrl + '/book/comment-progress';
	}
	putCommentProgress(): string {
		return this.customerUrl + '/book/comment-progress';
	}
	getCommentProgress(readListId: number): string {
		console.log(readListId);
		return this.customerUrl + '/book/comment-progress/' + readListId;
	}
	deleteCommentProgress(commentProgressId: number): string {
		return this.customerUrl + '/book/comment-progress/' + commentProgressId;
	}
	postIsShareCommentProgress(commentProgressId: number): string {
		return this.customerUrl + '/book/comment-progress/isshare/' + commentProgressId;
	}
}
