import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import { CustomerBookBoughtComponent } from './customer-reading-progress/customer-book-bought/customer-book-bought.component';
import { CustomerBookReadListDetailComponent } from './customer-reading-progress/customer-book-read-list-detail/customer-book-read-list-detail.component';
import { CustomerBookReadListComponent } from './customer-reading-progress/customer-book-read-list/customer-book-read-list.component';
import { CustomerReadingProgressComponent } from './customer-reading-progress/customer-reading-progress.component';

const routes: Routes = [
  {
    path: '',
    component: CustomerReadingProgressComponent,
    children:[
      {
				path: 'bought',
				component: CustomerBookBoughtComponent,
			},
      {
				path: 'read-list',
				component: CustomerBookReadListComponent,
			},
      {
				path: 'read-list/:readListId',
				component: CustomerBookReadListDetailComponent,
			},
    ]
  },
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class CustomerReadingProgressRoutingModule {}
