import {Observable} from 'rxjs';
import {PagingRequestBase} from './../../../models/common/page/pagingrequest/PagingRequestBase';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {CustomerUrlBook} from 'src/app/core/utils/URL/customer-url/customer-book/customer-url-book';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {BookBoughtResponse} from 'src/app/core/models/customer/book/response/BookBoughtResponse';
import {BookInReadList} from 'src/app/core/models/customer/book/response/BookInReadList';
import {BookInReadListDetail} from 'src/app/core/models/customer/book/response/BookInReadListDetail';
import {BookReadProgressRequest} from 'src/app/core/models/customer/book/request/BookReadProgressRequest';
import {UpdateBookReadProgressRequest} from 'src/app/core/models/customer/book/request/UpdateBookReadProgressRequest';
import { CommentProgressResponse } from 'src/app/core/models/customer/book/request/CommentProgressResponse';
@Injectable({
	providedIn: 'root'
})
export class BookService {
	languageCode = 'vn';
	constructor(private customerUrlBook: CustomerUrlBook, private http: HttpClient) {}
	getBookBought(request: PagingRequestBase): Observable<ApiResult<PagedResult<BookBoughtResponse>>> {
		request.languageCode = this.languageCode;
		return this.http.get<ApiResult<PagedResult<BookBoughtResponse>>>(this.customerUrlBook.getBookBought(request));
	}
	getBookInReadList(request: PagingRequestBase): Observable<ApiResult<PagedResult<BookInReadList>>> {
		request.languageCode = this.languageCode;
		return this.http.get<ApiResult<PagedResult<BookInReadList>>>(this.customerUrlBook.getBookInReadList(request));
	}
	getBookInReadListDetail(readListId: number): Observable<ApiResult<BookInReadListDetail>> {
		return this.http.get<ApiResult<BookInReadListDetail>>(this.customerUrlBook.getBookInReadListDetail(readListId));
	}
	postBookToReadList(productId: number): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlBook.postBookToReadList(), productId);
	}
	postReadProgress(request: BookReadProgressRequest): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlBook.postReadProgress(), request);
	}
	putReadProgress(request: UpdateBookReadProgressRequest): Observable<ApiResult<boolean>> {
		return this.http.put<ApiResult<boolean>>(this.customerUrlBook.putReadProgress(), request);
	}
	deleteBookFromReadList(productId: Number): Observable<ApiResult<boolean>> {
		return this.http.delete<ApiResult<boolean>>(this.customerUrlBook.deleteBookFromReadList(productId));
	}
	getCommentProgresses(readListId: number): Observable<ApiResult<Array<CommentProgressResponse>>> {
		return this.http.get<ApiResult<Array<CommentProgressResponse>>>(
			this.customerUrlBook.getCommentProgress(readListId)
		);
	}
	postCommentProgress(commentProgress: CommentProgressResponse): Observable<ApiResult<boolean>> {
		return this.http.post<ApiResult<boolean>>(this.customerUrlBook.postCommentProgress(), commentProgress);
	}
	putCommentProgress(commentProgress: CommentProgressResponse): Observable<ApiResult<boolean>> {
		return this.http.put<ApiResult<boolean>>(this.customerUrlBook.putCommentProgress(), commentProgress);
	}
	deleteCommentProgress(commentProgressId: number): Observable<ApiResult<boolean>> {
		return this.http.delete<ApiResult<boolean>>(this.customerUrlBook.deleteCommentProgress(commentProgressId));
	}
	postIsShareCommentProgress(commentProgressId: number): Observable<ApiResult<boolean>> {
		return this.http.get<ApiResult<boolean>>(this.customerUrlBook.postIsShareCommentProgress(commentProgressId));
	}
}
